body {
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif; }

button {
  cursor: pointer; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  bottom: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset 0%;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  display: flex;
  align-items: center;
  margin: 0 auto;
  padding: 20px 0 10px;
  width: 880px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  z-index: 5; }
  @media (max-width: 1023px) {
    .page-header {
      padding: 0;
      width: 100%;
      border: none; } }
  .page-header--closed .main-nav {
    display: none; }

.page-header__logo {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__logo {
      position: absolute;
      display: flex;
      left: 21px;
      top: 24px; } }
  @media (max-width: 767px) {
    .page-header__logo {
      left: 16px; } }

.page-header__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__toggle {
      display: block;
      position: fixed;
      top: 0;
      right: 0;
      z-index: 6;
      padding: 0;
      width: 50px;
      height: 50px;
      background-color: #0ad9c6;
      background-repeat: no-repeat;
      background-position: center;
      border: 0; } }

.page-header__toggle-line {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 22.5px;
  height: 1px;
  font-size: 0;
  color: transparent;
  background-color: #000;
  transition: 0.3s; }
  .page-header__toggle-line::after {
    top: 5px; }
  .page-header__toggle-line::before {
    top: -5px; }

.page-header__toggle-line::after,
.page-header__toggle-line::before {
  content: "";
  position: absolute;
  left: 0;
  width: 22.5px;
  height: 1px;
  background-color: #000;
  transform-origin: 50% 50%;
  transition: 0.3s, transform 0.3s; }

.page-header--opened .page-header__toggle {
  background-color: #fff; }

.page-header--opened .page-header__toggle-line {
  background: transparent; }

.page-header--opened .page-header__toggle-line::after,
.page-header--opened .page-header__toggle-line::before {
  transform: rotate3d(0, 0, 1, 45deg);
  transition: 0.3s, transform 0.3s 0.3s;
  top: 0;
  left: 0;
  width: 22.5px;
  height: 1px;
  background-color: rgba(20, 21, 23, 0.2); }

.page-header--opened .page-header__toggle-line::before {
  transform: rotate3d(0, 0, 1, -45deg); }

.page-header--opened .page-header__toggle-line .page-header__toggle-line:hover {
  background: transparent; }

